<template>
  <div>
    <br>
    <br>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
      <el-form-item label="Ïù¥Î©îÏùº" prop="username">
        <el-input v-model="ruleForm.username"></el-input>
      </el-form-item>
      <el-form-item label="ÎπÑÎ∞ÄÎ≤àÌò∏" prop="password">
        <el-input type="password" v-model="ruleForm.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="login('ruleForm')">Î°úÍ∑∏Ïù∏</el-button>
        <el-button @click="resetForm('ruleForm')">ÌöåÏõêÍ∞ÄÏûÖ</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Login",
  data(){
    return {
      ruleForm: {
        username: '',
        password: ''
      }
    }
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      console.log(this.$store.state.allUsers)
    },
    login() {
      axios.post('/api/authenticate', {
        username: this.ruleForm.username,
        password: this.ruleForm.password
      })
        .then(res => {
          console.log(res)
          if(res !== null) {
            this.$store.commit('setToken', res)
            this.$router.push('/')
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>

<style scoped>

</style>



