import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({

  state: {//data
    allUsers: [
      {username: 'tommy', password: '1q2w3e4r'},
      {username: 'jimmy', password: '1q2w3e4r'}
    ],
    getToken: ''
  },
  getters: { //computed Ïó≠Ìï†
    allUsersCount: state => { //allUsersÏùò Ïà´Ïûê
      return state.allUsers.length
    },
    findByJimmy: state => { //allUsers Ï§ë Ïù¥Î¶ÑÏù¥ jimmyÏù∏ Ïà´Ïûê
      let count = 0;
      state.allUsers.forEach(arr => {
        if(arr.username === 'jimmy') {
          count ++
        }
      })
      return count
    },
    getToken: state => {
      return state.getToken
    }
  },
  mutations: { //state Í∞íÏùÑ Î≥ÄÌôîÏãúÏºú Ï§ÄÎã§.
    addUser: (state, payload) => {
      state.allUsers.push(payload)//payloadÎ•º ÎÑ£Ïñ¥Ï§ÄÎã§.
    },
    setToken: (state, payload) => {
      state.getToken = payload
    }
  },
  actions: {

  }
});



